// Notification component - TypeScript
// A reusable notification component

type NotificationType = 'info' | 'success' | 'warning' | 'error';

interface NotificationOptions {
    type?: NotificationType;
    duration?: number;
    closable?: boolean;
}

class Notification {
    private container: HTMLElement | null = null;

    constructor() {
        this.ensureContainer();
    }

    private ensureContainer(): void {
        this.container = document.getElementById('notification-container');
        if (!this.container) {
            this.container = document.createElement('div');
            this.container.id = 'notification-container';
            this.container.className = 'notification-container';
            document.body.appendChild(this.container);
        }
    }

    show(message: string, options: NotificationOptions = {}): void {
        const { type = 'info', duration = 5000, closable = true } = options;

        const notification = document.createElement('div');
        notification.className = `notification notification--${type}`;
        notification.innerHTML = `
            <span class="notification__message">${message}</span>
            ${closable ? '<button class="notification__close">&times;</button>' : ''}
        `;

        if (closable) {
            const closeBtn = notification.querySelector('.notification__close');
            closeBtn?.addEventListener('click', () => this.remove(notification));
        }

        this.container?.appendChild(notification);

        // Trigger animation
        requestAnimationFrame(() => {
            notification.classList.add('notification--visible');
        });

        if (duration > 0) {
            setTimeout(() => this.remove(notification), duration);
        }
    }

    private remove(notification: HTMLElement): void {
        notification.classList.remove('notification--visible');
        setTimeout(() => notification.remove(), 300);
    }

    info(message: string, options?: Omit<NotificationOptions, 'type'>): void {
        this.show(message, { ...options, type: 'info' });
    }

    success(message: string, options?: Omit<NotificationOptions, 'type'>): void {
        this.show(message, { ...options, type: 'success' });
    }

    warning(message: string, options?: Omit<NotificationOptions, 'type'>): void {
        this.show(message, { ...options, type: 'warning' });
    }

    error(message: string, options?: Omit<NotificationOptions, 'type'>): void {
        this.show(message, { ...options, type: 'error' });
    }
}

// Create singleton instance
const notification = new Notification();

// Export for ES modules
export { notification, Notification, NotificationType, NotificationOptions };

// Make available globally
(window as any).Notification = notification;
